@import "./utils.scss/variables";

.watch {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;

  &__container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: $watch-size;
    height: $watch-size;
    border-radius: $border-radius;
    border: 4px solid red;
  }

  &__center {
    width: $center-size;
    height: $center-size;
    border-radius: $border-radius;
    background-color: $color-main;
  }

  &__arrow {
    position: absolute;
    bottom: 50%;

    border-left: 3px solid $color-main;
    border-radius: $arrow-border;
    height: $arrow-height;

    transform-origin: center bottom;
    animation: move 3600s steps(60) infinite;

    &--seconds {
      height: $arrow-height--seconds;
      border-left: 2px solid $color-main;
      animation: move 60s steps(60) infinite;
    }
  }
}
